import React from "react";
import Head from "next/head";
import { makeStyles, Theme } from "@material-ui/core/styles";
import AppBar from "@material-ui/core/AppBar";
import Tabs from "@material-ui/core/Tabs";
import Tab from "@material-ui/core/Tab";
import RoomIcon from "@material-ui/icons/Room";
import SwapHorizontalCircleIcon from "@material-ui/icons/SwapHorizontalCircle";
import ExploreIcon from "@material-ui/icons/Explore";
import Toolbar from "@material-ui/core/Toolbar";
import Typography from "@material-ui/core/Typography";
import SwipeableViews from "react-swipeable-views";

import CoordinateCurrent from "components/CoordinateCurrent";
import CoordinateCalculator from "components/CoordinateCalculator";
import Compass from "components/Compass";
import {
  HEAD_TITLE,
  TAB_COORDINATE_CURRENT,
  TAB_COORDINATE_CALCULATOR,
  TAB_COMPASS
} from "utils/constants";

function a11yProps(index: any) {
  return {
    id: `scrollable-force-tab-${index}`,
    "aria-controls": `scrollable-force-tabpanel-${index}`
  };
}

const useStyles = makeStyles((theme: Theme) => ({
  root: {
    flexGrow: 1,
    width: "100%",
    backgroundColor: theme.palette.background.paper
  },
  title: {
    flexGrow: 1
  },
  content: {
    height: `calc(100vh - 72px - 56px)`,
    overflow: "scroll",
    position: "relative",

    [`${theme.breakpoints.up("sm")}`]: {
      height: `calc(100vh - 72px - 64px)`
    }
  }
}));

export default function ScrollableTabsButtonForce() {
  const classes = useStyles();
  const [tabIndex, setTabIndex] = React.useState(0);

  const handleChange = (_: React.ChangeEvent<{}>, newValue: number) => {
    setTabIndex(newValue);
  };

  return (
    <div className={classes.root}>
      <AppBar position="static">
        <Toolbar>
          <Head>
            <title>{HEAD_TITLE}</title>
          </Head>
          <Typography variant="h6" component="h1" className={classes.title}>
            {HEAD_TITLE}
          </Typography>
        </Toolbar>
      </AppBar>
      <div className={classes.content}>
        <SwipeableViews
          enableMouseEvents
          index={tabIndex}
          onChangeIndex={index => setTabIndex(index)}
        >
          <CoordinateCurrent />
          <CoordinateCalculator />
          <Compass />
        </SwipeableViews>
      </div>
      <AppBar position="static" color="default">
        <Tabs
          value={tabIndex}
          onChange={handleChange}
          variant="scrollable"
          scrollButtons="on"
          indicatorColor="primary"
          textColor="primary"
          aria-label="scrollable force tabs"
        >
          <Tab
            label={TAB_COORDINATE_CURRENT}
            icon={<RoomIcon />}
            {...a11yProps(0)}
          />
          <Tab
            label={TAB_COORDINATE_CALCULATOR}
            icon={<SwapHorizontalCircleIcon />}
            {...a11yProps(1)}
          />
          <Tab label={TAB_COMPASS} icon={<ExploreIcon />} {...a11yProps(2)} />
        </Tabs>
      </AppBar>
    </div>
  );
}
